import React, { useState, useRef } from 'react';
import {
  Box,
  Typography,
  Button,
  CircularProgress,
  Alert,
  AlertTitle,
  useTheme,
  Chip,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Badge
} from '@mui/material';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import DescriptionIcon from '@mui/icons-material/Description';
import DeleteIcon from '@mui/icons-material/Delete';
import { styled } from '@mui/material/styles';
import { analyzeChatHistory, analyzeMultipleChats, hasApiKey } from '../services/geminiService';

interface FileUploadProps {
  onAnalysisComplete: (result: string) => void;
}

const UploadBox = styled(Box, {
  shouldForwardProp: (prop) => prop !== 'isDragging' && prop !== 'isDarkMode',
})<{ isDragging: boolean; isDarkMode: boolean }>(({ isDragging, isDarkMode, theme }) => ({
  border: `2px dashed ${isDarkMode 
    ? isDragging ? theme.palette.primary.main : 'rgba(255, 255, 255, 0.3)' 
    : isDragging ? theme.palette.primary.dark : theme.palette.primary.main}`,
  borderRadius: theme.shape.borderRadius,
  padding: theme.spacing(4),
  textAlign: 'center',
  cursor: 'pointer',
  transition: 'all 0.3s ease',
  backgroundColor: isDarkMode
    ? isDragging ? 'rgba(33, 150, 243, 0.08)' : 'rgba(255, 255, 255, 0.05)'
    : isDragging ? theme.palette.action.hover : theme.palette.background.paper,
  marginTop: theme.spacing(3),
  '&:hover': {
    borderColor: theme.palette.primary.main,
    backgroundColor: isDarkMode 
      ? 'rgba(33, 150, 243, 0.08)' 
      : theme.palette.action.hover,
  },
}));

const FileUpload: React.FC<FileUploadProps> = ({ onAnalysisComplete }) => {
  const theme = useTheme();
  const isDarkMode = theme.palette.mode === 'dark';
  
  const [isDragging, setIsDragging] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [processedFiles, setProcessedFiles] = useState<{name: string, content: string}[]>([]);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleDragEnter = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(true);
  };

  const handleDragLeave = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(false);
  };

  const handleDragOver = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    setIsDragging(true);
  };

  const validateFile = (file: File): boolean => {
    //   
    const allowedTypes = ['application/json', 'text/html'];
    const allowedExtensions = ['.json', '.html'];
    const hasValidExtension = allowedExtensions.some(ext => file.name.toLowerCase().endsWith(ext));
    
    if (!allowedTypes.includes(file.type) && !hasValidExtension) {
      return false;
    }

    //    (  10 )
    const maxSize = 10 * 1024 * 1024; // 10   
    if (file.size > maxSize) {
      return false;
    }

    return true;
  };

  const extractChatContent = async (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      
      reader.onload = (event) => {
        try {
          const content = event.target?.result as string;
          
          // , JSON  
          if (file.name.endsWith('.json')) {
            try {
              const jsonData = JSON.parse(content);
              //    JSON  Telegram
              if (jsonData.messages) {
                const messages = jsonData.messages
                  .map((msg: any) => {
                    //  ,    
                    const from = msg.from || 'Unknown';
                    const date = msg.date || '';
                    const text = Array.isArray(msg.text) 
                      ? msg.text.map((t: any) => (typeof t === 'string' ? t : t.text)).join('')
                      : msg.text || '';
                    
                    return `${from} (${date}): ${text}`;
                  })
                  .join('\n');
                
                resolve(messages);
              } else {
                reject(new Error('  JSON  Telegram'));
              }
            } catch (e) {
              reject(new Error('   JSON'));
            }
          } 
          //  HTML
          else if (file.name.endsWith('.html')) {
            const parser = new DOMParser();
            const doc = parser.parseFromString(content, 'text/html');
            const messages = Array.from(doc.querySelectorAll('.message')).map(msg => {
              const from = msg.querySelector('.from_name')?.textContent || 'Unknown';
              const text = msg.querySelector('.text')?.textContent || '';
              const date = msg.querySelector('.date')?.textContent || '';
              
              return `${from} (${date}): ${text}`;
            }).join('\n');
            
            if (messages) {
              resolve(messages);
            } else {
              reject(new Error('     HTML'));
            }
          } else {
            reject(new Error('  '));
          }
        } catch (error) {
          reject(error);
        }
      };
      
      reader.onerror = () => {
        reject(new Error('   '));
      };
      
      reader.readAsText(file);
    });
  };

  const processFiles = async (files: File[]) => {
    //   API 
    if (!hasApiKey()) {
      setError('  API  Gemini  ');
      return;
    }
    
    setIsLoading(true);
    setError(null);
    
    const invalidFiles: string[] = [];
    const validFiles: File[] = [];
    
    //     
    for (const file of files) {
      if (validateFile(file)) {
        validFiles.push(file);
      } else {
        invalidFiles.push(file.name);
      }
    }
    
    //    ,  
    if (invalidFiles.length > 0) {
      console.warn('    -    :', invalidFiles);
    }
    
    //    ,  
    if (validFiles.length === 0) {
      setError('    .    .html  .json   10 .');
      setIsLoading(false);
      return;
    }
    
    try {
      //   
      const processedContents: {name: string, content: string}[] = [];
      
      for (const file of validFiles) {
        try {
          const chatContent = await extractChatContent(file);
          
          //    ,  
          if (chatContent.length < 100) {
            invalidFiles.push(file.name + ' (  )');
            continue;
          }
          
          processedContents.push({
            name: file.name,
            content: chatContent
          });
        } catch (fileError) {
          console.error(`    ${file.name}:`, fileError);
          invalidFiles.push(file.name + ' ( )');
        }
      }
      
      //        
      if (processedContents.length === 0) {
        throw new Error('       .');
      }
      
      //     
      setProcessedFiles(processedContents);
      
      //    
      const analysis = await analyzeMultipleChats(processedContents.map(file => file.content));
      onAnalysisComplete(analysis);
    } catch (error: any) {
      console.error('   :', error);
      setError(error instanceof Error ? error.message : '  ');
    } finally {
      setIsLoading(false);
    }
  };

  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    setIsDragging(false);
    
    if (e.dataTransfer.files && e.dataTransfer.files.length > 0) {
      const filesArray = Array.from(e.dataTransfer.files);
      processFiles(filesArray);
    }
  };

  const handleFileInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files.length > 0) {
      const filesArray = Array.from(e.target.files);
      processFiles(filesArray);
    }
  };

  const handleClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click();
    }
  };

  const handleButtonClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    handleClick();
  };
  
  const handleRemoveFile = (index: number) => {
    const newFiles = [...processedFiles];
    newFiles.splice(index, 1);
    setProcessedFiles(newFiles);
    
    //    ,  
    if (newFiles.length === 0) {
      fileInputRef.current!.value = '';
    }
  };
  
  const handleAnalyze = async () => {
    if (processedFiles.length === 0) {
      setError('   ');
      return;
    }
    
    setIsLoading(true);
    setError(null);
    
    try {
      const analysis = await analyzeMultipleChats(processedFiles.map(file => file.content));
      onAnalysisComplete(analysis);
    } catch (error: any) {
      console.error('   :', error);
      setError(error instanceof Error ? error.message : '  ');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Box sx={{ my: 3 }}>
      {!hasApiKey() && (
        <Alert 
          severity="warning" 
          sx={{ 
            mb: 2,
            ...(isDarkMode && {
              backgroundColor: 'rgba(255, 152, 0, 0.1)',
              color: 'warning.light'
            })
          }}
        >
          <AlertTitle> API </AlertTitle>
          ,  API  Gemini,      .
        </Alert>
      )}
      
      <input
        type="file"
        ref={fileInputRef}
        style={{ display: 'none' }}
        accept=".html,.json"
        onChange={handleFileInputChange}
        multiple
      />
      
      {processedFiles.length > 0 ? (
        <Box sx={{ mb: 3, mt: 2 }}>
          <Typography variant="h6" sx={{ mb: 1 }}>
              ({processedFiles.length})
          </Typography>
          <List sx={{ 
            bgcolor: isDarkMode ? 'rgba(255, 255, 255, 0.05)' : 'background.paper',
            borderRadius: 1,
            mb: 2
          }}>
            {processedFiles.map((file, index) => (
              <ListItem 
                key={index}
                secondaryAction={
                  <Button 
                    size="small" 
                    color="error"
                    onClick={() => handleRemoveFile(index)}
                    startIcon={<DeleteIcon />}
                  >
                    
                  </Button>
                }
              >
                <ListItemIcon>
                  <DescriptionIcon color="primary" />
                </ListItemIcon>
                <ListItemText 
                  primary={file.name} 
                  secondary={`${Math.round(file.content.length / 100) / 10} KB`} 
                />
              </ListItem>
            ))}
          </List>
          
          <Box sx={{ display: 'flex', gap: 2, justifyContent: 'space-between' }}>
            <Button
              variant="outlined"
              onClick={handleClick}
              startIcon={<CloudUploadIcon />}
              disabled={isLoading}
            >
                
            </Button>
            
            <Button
              variant="contained"
              color="primary"
              onClick={handleAnalyze}
              disabled={isLoading || processedFiles.length === 0}
            >
              {isLoading ? '...' : '  '}
            </Button>
          </Box>
        </Box>
      ) : (
        <UploadBox
          isDragging={isDragging}
          isDarkMode={isDarkMode}
          onDragOver={handleDragOver}
          onDragLeave={handleDragLeave}
          onDrop={handleDrop}
          onClick={handleClick}
        >
          <CloudUploadIcon sx={{ fontSize: 48, color: 'primary.main', mb: 2 }} />
          <Typography variant="h6" gutterBottom>
                  
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
             : HTML, JSON (  Telegram)
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
              : 10 
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                
          </Typography>
          <Button
            variant="contained"
            startIcon={<CloudUploadIcon />}
            onClick={handleButtonClick}
            disabled={isLoading}
          >
             
          </Button>
        </UploadBox>
      )}
      
      {isLoading && (
        <Box 
          sx={{ 
            display: 'flex', 
            flexDirection: 'column',
            alignItems: 'center', 
            my: 3, 
            gap: 2 
          }}
        >
          <CircularProgress size={40} />
          <Typography variant="body2" color="text.secondary">
              ...
          </Typography>
        </Box>
      )}
      
      {error && (
        <Alert 
          severity="error" 
          sx={{ 
            mt: 2,
            ...(isDarkMode && {
              backgroundColor: 'rgba(211, 47, 47, 0.1)',
              color: 'error.light'
            })
          }}
        >
          <AlertTitle></AlertTitle>
          {error}
        </Alert>
      )}
    </Box>
  );
};

export default FileUpload; 